:root {
    --sprotty-background: var(--theia-layout-color3);
    --sprotty-edge: var(--theia-editor-foreground);
    --sprotty-border: var(--theia-editor-foreground);
}

svg {
    border-style: solid;
    border-width: 1px;
    border-color: #bbb;
}

.header {
    font-weight: bold;
    padding: 20px;
    margin: 2rem;
}

.sprotty-node {
    fill: inherit;
}

.sprotty-edge {
    fill: #000;
}

.course-node {
    fill: #f1bd6c;
}

.caption-node {
    fill: white;
}

.label-edge {
    fill: white;
    stroke: white;
}

.properties-node {
    fill: #d3e3e3;
}

.feature-node {
    fill: #f1bd6c;
}

.imported-concept-node {
    fill: #fabfa4;
    stroke-dasharray: 5, 5;
    border: 2px dashed #000; /* Ajusta el grosor, estilo y color del borde */
    padding: 10px;
}

.border {
    border: 2px dashed #000; /* Ajusta el grosor, estilo y color del borde */
    padding: 10px;
}

.topic-node {
    fill: #f5f5f5;
}

.mouseover {
    box-shadow: white;
}

.selected {
    fill: white;
}

.line-uses {
    stroke: black;
}

.line-specializes {
    stroke: gray;
}

.concept-node {
    fill: #fffcdf;
    stroke-width: 2px;
    font-weight: bold;
    border: 2px #000;
}

.border-stars {
    stroke: var(--sprotty-border);
}

.uses-edge {
    stroke: black;
}

.uses-arrow {
    fill: black;
    stroke: black;
}

.uses-topic-edge {
    stroke: black;
    stroke-dasharray: 5, 5;
}

.arrow-uses-topic {
    fill: black;
    stroke: black;
}

.specializes-edge {
    stroke: black;
}

.specializes-arrow {
    stroke: black;
    fill: #f5f5f5;
}

.p-MenuBar-item:nth-child(4), .p-MenuBar-item:nth-child(6), .p-MenuBar-item:nth-child(7), .p-MenuBar-item:nth-child(8) {
    display: none;
}

